import React, { useState, useContext, useEffect } from 'react';
import AlertContext from './../../context/alert/alertContext';
import AuthContext from './../../context/auth/authContext';
const Register = (props) => {
	const alertContext = useContext(AlertContext);
	const authContext = useContext(AuthContext);
	const { setAlert } = alertContext;
	const { register, error, clearErrors, isAuthenticated } = authContext;
	const [ user, setUser ] = useState({
		name: '',
		email: '',
		password: '',
		password2: ''
	});
	useEffect(
		() => {
			if (error) {
				setAlert(error, 'danger');
				clearErrors();
			}
			if (isAuthenticated) {
				props.history.push('/');
			}
		},
		// eslint-disable-next-line
		[ isAuthenticated, error, props.history ]
	);

	const onChange = (e) => {
		setUser({ ...user, [e.target.name]: e.target.value });
	};
	const onSubmit = (e) => {
		e.preventDefault();
		if (name === '' || email === '' || password === '') {
			setAlert('Please enter all Fields', 'danger');
		} else if (password !== password2) {
			setAlert('Passwords do not match', 'danger');
		} else {
			register(user);
		}
	};
	const { name, email, password, password2 } = user;
	return (
		<div className="form-container">
			<h1>
				Account <span className="text-primary">Register</span>
			</h1>
			<form onSubmit={onSubmit}>
				<div className="form-group">
					<label htmlFor="name">Name</label>
					<input value={name} type="text" name="name" required onChange={onChange} />
				</div>
				<div className="form-group">
					<label htmlFor="email">Email</label>
					<input value={email} type="email" name="email" required onChange={onChange} />
				</div>
				<div className="form-group">
					<label htmlFor="password">Password</label>
					<input
						value={password}
						type="password"
						required
						name="password"
						onChange={onChange}
						minLength={6}
					/>
				</div>
				<div className="form-group">
					<label htmlFor="password2">Confirm Password</label>
					<input value={password2} type="password" required name="password2" onChange={onChange} />
				</div>
				<input type="submit" value="Register" className="btn btn-block btn-primary" />
			</form>
		</div>
	);
};

export default Register;
