import React, { useState, useContext, useEffect } from 'react';
import AuthContext from './../../context/auth/authContext';
import AlertContext from './../../context/alert/alertContext';

const Login = (props) => {
	const authContext = useContext(AuthContext);
	const alertContext = useContext(AlertContext);
	const { loginUser, error, clearErrors, isAuthenticated } = authContext;
	const { setAlert } = alertContext;
	useEffect(
		() => {
			if (error) {
				setAlert(error, 'danger');
				clearErrors();
			}
			if (isAuthenticated) {
				props.history.push('/');
			}
		},
		// eslint-disable-next-line
		[ isAuthenticated, error, props.history ]
	);
	const [ user, setUser ] = useState({
		email: '',
		password: ''
	});
	const { email, password } = user;
	const onChange = (e) => {
		setUser({ ...user, [e.target.name]: e.target.value });
	};
	const onSubmit = (e) => {
		e.preventDefault();
		if (email === '' || password === '') {
			setAlert('Please Fill in all Fields', 'danger');
		} else loginUser(user);
	};

	return (
		<div className="form-container">
			<h1>
				Account <span className="text-primary">Login</span>
			</h1>
			<form onSubmit={onSubmit}>
				<div className="form-group">
					<label htmlFor="email">Email</label>
					<input required value={email} type="email" name="email" onChange={onChange} />
				</div>
				<div className="form-group">
					<label htmlFor="password">Password</label>
					<input required value={password} type="password" name="password" onChange={onChange} />
				</div>
				<input type="submit" value="Login" className="btn btn-block btn-primary" />
			</form>
		</div>
	);
};

export default Login;
