import React, { useState, useContext, useEffect } from 'react';
import ContactContext from './../../context/contact/contactContext';
const ContactForm = () => {
	const contactContext = useContext(ContactContext);
	const { addContact, current, clearCurrent, updateContact } = contactContext;
	useEffect(
		() => {
			if (current !== null) {
				setContact(current);
			} else {
				setContact({
					name: '',
					email: '',
					phone: '',
					type: 'personal'
				});
			}
		},
		[ current, contactContext ]
	);
	const [ contact, setContact ] = useState({
		name: '',
		email: '',
		phone: '',
		type: 'personal'
	});

	const onChange = (e) => {
		const name = e.target.name;
		setContact({ ...contact, [name]: e.target.value });
	};
	const clearAll = () => {
		clearCurrent();
	};
	const onSubmit = (e) => {
		e.preventDefault();
		if (!current) addContact(contact);
		else updateContact(contact);
		setContact({
			name: '',
			email: '',
			phone: '',
			type: 'personal'
		});
	};
	const { name, email, type, phone } = contact;
	return (
		<form onSubmit={onSubmit}>
			<h2 className="text-primary">{current ? 'Edit Contact' : 'Add Contact'}</h2>
			<input type="text" placeholder="Name" name="name" value={name} onChange={onChange} />
			<input type="email" className="" placeholder="Email" name="email" value={email} onChange={onChange} />
			<input type="text" placeholder="Phone" name="phone" value={phone} onChange={onChange} />
			<h5>Contact Type</h5>
			<input type="radio" name="type" value="personal" checked={type === 'personal'} onChange={onChange} />
			Personal{' '}
			<input
				type="radio"
				name="type"
				value="professional"
				checked={type === 'professional'}
				onChange={onChange}
			/>
			Professional {' '}
			<div>
				<input
					type="submit"
					className="btn btn-primary btn-block"
					value={current ? 'Save Changes' : 'Add Contact'}
				/>
			</div>
			{current && (
				<div>
					<button onClick={clearAll} className="btn btn-light btn-block">
						Clear
					</button>
				</div>
			)}
		</form>
	);
};

export default ContactForm;
